import React from 'react'
import { Container, Row, Col } from 'react-bootstrap'
import LayoutCustom from "../components/LayoutCustom";
import Footer from "../components/Footer";

class ContactUs extends React.Component {

    componentDidMount() {
        var x, i, j, l, ll, selElmnt, a, b, c

        /* look for any elements with the class "custom-select" */
        x = document.getElementsByClassName("custom-select")
        l = x.length

        for (i = 0; i < l; i++) {
            selElmnt = x[i].getElementsByTagName("select")[0]
            ll = selElmnt.length

            /* for each element, create a new div that will act as the selected item */
            a = document.createElement("div")
            a.setAttribute("class", "select-selected app-selected-default")
            a.innerHTML = selElmnt.options[selElmnt.selectedIndex].innerHTML
            x[i].appendChild(a)

            /* for each element, create a new div that will contain the option list */
            b = document.createElement("div")
            b.setAttribute("class", "select-items select-hide")

            for (j = 1; j < ll; j++) {
                /* for each option in the original select element, create a new div that will act as an option item */
                c = document.createElement("div")
                c.innerHTML = selElmnt.options[j].innerHTML

                c.addEventListener("click", function (e) {
                    /* when an item is clicked, update the original select box, and the selected item */
                    var y, i, k, s, h, sl, yl
                    s = this.parentNode.parentNode.getElementsByTagName("select")[0]
                    sl = s.length
                    h = this.parentNode.previousSibling

                    for (i = 0; i < sl; i++) {
                        if (s.options[i].innerHTML === this.innerHTML) {
                            s.selectedIndex = i
                            h.innerHTML = this.innerHTML
                            y = this.parentNode.getElementsByClassName("same-as-selected")
                            yl = y.length

                            for (k = 0; k < yl; k++) {
                                y[k].removeAttribute("class")
                            }

                            this.setAttribute("class", "same-as-selected")
                            break
                        }
                    }
                    h.click()
                })
                b.appendChild(c)
            }
            x[i].appendChild(b)

            a.addEventListener("click", function (e) {
                /* when the select box is clicked, close any other select boxes, and open/close the current select box:*/
                e.stopPropagation()
                closeAllSelect(this)
                this.nextSibling.classList.toggle("select-hide")
                this.classList.toggle("select-arrow-active")
                if (a.innerHTML === "I’m interested in…") {
                    this.classList.remove("app-selected-default");
                    this.classList.add("app-selected-default");
                } else {
                    this.classList.remove("app-selected-default");
                }
            })
        }

        function closeAllSelect(elmnt) {
            /* a function that will close all select boxes in the document, except the current select box */
            var x, y, i, xl, yl, arrNo = []
            x = document.getElementsByClassName("select-items")
            y = document.getElementsByClassName("select-selected")
            xl = x.length
            yl = y.length

            for (i = 0; i < yl; i++) {
                if (elmnt === y[i]) {
                    arrNo.push(i)
                } else {
                    y[i].classList.remove("select-arrow-active")
                }
            }

            for (i = 0; i < xl; i++) {
                if (arrNo.indexOf(i)) {
                    x[i].classList.add("select-hide")
                }
            }
        }

        /* if the user clicks anywhere outside the select box, then close all select boxes */
        document.addEventListener("click", closeAllSelect)
    }

    render() {
        return (
            <LayoutCustom>
                <div className="main-content-padding">
                    <div className="app-general-seciton app-section-contactus" data-sal="fade" data-sal-duration="1000" data-sal-delay="100" data-sal-easing="ease">
                        <Container>
                            <div className="app-title">
                                <h2>Contact Us</h2>
                            </div>
                            <div className="app-contactus-detail">
                                <Row>
                                    <Col xs={12} md={12} lg={5}>
                                        <form>
                                            <div className="app-contactus-left">
                                                <input type="text" placeholder="Name" />
                                                <input
                                                    type="text"
                                                    placeholder="Email"
                                                />
                                                <input
                                                    type="text"
                                                    placeholder="Subject"
                                                />
                                                <div className="custom-select">
                                                    <select>
                                                        <option value="">I’m interested in…</option>
                                                        <option value="Appvity">Appvity</option>
                                                        <option value="eProducts">eProducts</option>
                                                        <option value="Services">Services</option>
                                                        <option value="Sales">Sales</option>
                                                        <option value="Support">Support</option>
                                                        <option value="Technical concern">Technical concern</option>
                                                        <option value="Others">Others</option>
                                                    </select>
                                                </div>
                                                <textarea
                                                    rows="6"
                                                    placeholder="Message"
                                                ></textarea>
                                                <input
                                                    className="app-contactus-btn"
                                                    type="submit"
                                                    value="Send Message"
                                                />
                                            </div>
                                        </form>
                                    </Col>
                                    <Col xs={12} md={12} lg={7}></Col>
                                </Row>
                            </div>
                        </Container>
                        <div className="footer-custom">
                            <Footer />
                        </div>
                    </div>
                </div>
            </LayoutCustom>
        );
    }
}

export default ContactUs